var load = function load(src) {
  return new Promise(function (resolve, reject) {
    var img = new window.Image();
    img.addEventListener("load", function () {
      return resolve(img);
    }, false);
    img.addEventListener("error", reject, false);
    img.src = src;
  });
};
/*
 * Resize the image based on the given height or width boundary.
 * Auto resize based on aspect ratio.
 **/


var resize = function resize(targetWidth, targetHeight) {
  return function (width, height) {
    if (!targetWidth && !targetHeight) return {
      width: width,
      height: height
    };
    var originalAspectRatio = width / height;
    var targetAspectRatio = targetWidth / targetHeight;
    var outputWidth, outputHeight;

    if (originalAspectRatio > targetAspectRatio) {
      outputWidth = Math.min(width, targetWidth);
      outputHeight = outputWidth / originalAspectRatio;
    } else {
      outputHeight = Math.min(height, targetHeight);
      outputWidth = outputHeight * originalAspectRatio;
    }

    return {
      width: outputWidth,
      height: outputHeight
    };
  };
};

export default {
  load: load,
  resize: resize
};